body {
  background: #e0e5ec;
}
.page {
  padding: 0 15px;
}
.number {
  padding: 10px;
  text-align: center;
  color: #fc9b2d;
  font-size: 18px;
}
.card {
  color: #507DDC;
}
.card__body {
  background-color: #fff;
  border-radius: 10px;
  padding: 15px;
  font-size: 18px;
}
.card__body .question {
  margin-bottom: 30px;
}
.card__body .answer {
  border-radius: 12px;
  padding: 5px 0;
  text-align: center;
  border: 1px solid #507DDC;
  margin-top: 20px;
}
.card__body .answer__chosen {
  background: #5ac3fa;
  border-radius: 12px;
  padding: 5px 0;
  text-align: center;
  border: 1px solid #507DDC;
  margin-top: 20px;
}
.card__footer {
  border-radius: 10px;
  background: #6acccb;
  height: 100px;
}
.step {
  color: #fc9b2d;
  display: flex;
  margin-top: 10px;
  font-size: 18px;
}
.step .center {
  flex: 1;
}
